<template>
    <v-app>
            <v-navigation-drawer
                    app
                    :value="show"
                    @input="syncShow"
            >
                <v-list-item class="py-2">
                    <v-list-item-content>
                        <v-btn text class="text-left pa-0">
                            <v-list-item-title
                                    style="cursor: pointer"
                                    class="text-h5 font-weight-bold"
                                    @click="$router.push({path: '/'})"
                            >
                                급여 관리
                            </v-list-item-title>
                        </v-btn>
                        <v-list-item-subtitle class="text-caption">
                            <span style="letter-spacing: -0.04em; margin-left: 0.2em;">급여 명세서 메일 발송</span>
                        </v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list
                        dense
                        nav
                        class="mt-4"
                >
                    <v-list-item
                            v-for="item in items"
                            :key="item.title"
                            link
                            @click="$router.push({ path: item.path })"
                    >
                        <v-list-item-icon>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-item-icon>

                        <v-list-item-content>
                            <v-list-item-title class="text-body-1">{{ item.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-navigation-drawer>
            <v-app-bar
                    app
                    flat
                    hide-on-scroll
                    dense
            >
                <v-app-bar-nav-icon @click="show = !show"></v-app-bar-nav-icon>
            </v-app-bar>
            <v-divider></v-divider>
            <v-main>
                <v-sheet class="pa-5">
                    <v-slide-x-transition hide-on-leave>
                        <router-view></router-view>
                    </v-slide-x-transition>
                </v-sheet>
            </v-main>
    </v-app>
</template>
<script>
    export default {
        data () {
            return {
                items: [
                    { title: '급여 지급내역', icon: 'mdi-folder-open-outline', path: '/list' },
                    { title: '급여 업로드', icon: 'mdi-folder-upload-outline', path: '/upload' },
                    { title: '급여 명세서 발송', icon: 'mdi-email-outline', path: '/send_mail' },
                ],
                right: null,
                show: true,
            }
        },
        methods: {
            syncShow(e) {
                this.show = e
            },
        }
    }
</script>